/* @import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap'); */
/* @import url('https://fonts.googleapis.com/css2?family=Amiri:wght@400;700&family=Poppins:wght@400;500;600&display=swap'); */
@import url('https://fonts.googleapis.com/css2?family=Amiri:wght@400;700&family=Poppins:wght@400;500;600&family=Vazirmatn:wght@400;500;700&display=swap');
/*** font-family: 'Amiri', serif; 
     font-family: 'Vazirmatn', sans-serif; ***/
:root {
    --header-height: 3.5rem;
    /*color mode hsl(hue,saturation%,lightness%)*/
    /*
        purple: hsl(250, 66%, 75%)
        blue: hsl(207, 90%, 72%)
        pink: hsl(356, 66%, 75%)
        teal: hsl(174, 63%,62%)
    */
    --first-hue: 207;
    --sat: 90%;
    --lig: 72%;
    --second-hue: 219;

    --first-color: hsl(var(--first-hue), var(--sat), var(--lig));
    --first-color-alt: hsl(var(--first-hue), var(--sat), calc(var(--lig) - 4%));/*-4%*/
    --title-color: hsl(var(--second-hue), 15%, 95%);
    --text-color: hsl(var(--second-hue), 8%, 75%);
    --text-color-light: hsl(var(--second-hue), 4%, 55%);
    --body-color: hsl(var(--second-hue), 48%, 8%);
    --container-color: hsl(var(--second-hue), 32%, 12%);
    --body-font: 'Poppins', sans-serif;
    --biggest-font-size: 1.75rem;
    --h1-font-size: 1.5rem;
    --h2-font-size: 1.25rem;
    --h3-font-size: 1rem;
    --normal-font-size: .938rem;
    --small-font-size: .813rem;
    --smaller-font-size: .75rem;
    --tiny-font-size: .625rem;
    --font-medium: 500;
    --font-semibold: 600;
    --z-tooltap: 10;
    --z-fixed: 100;
    --z-modal: 1000;
}
::selection {
    background-color: var(--first-color);
    color: white;
}
@media screen and (min-width: 968px) {
    :root {
        --biggest-font-size: 2.5rem;
        --h1-font-size: 2.25rem;
        --h2-font-size: 1.5rem;
        --h3-font-size: 1.25rem;
        --normal-font-size: 1rem;
        --small-font-size: .875rem;
        --smaller-font-size: .813rem;
    }
}
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
html {
    scroll-behavior: smooth;
}
body,
button,
input,
textarea {
    font-family: var(--body-font);
    font-size: var(--normal-font-size);
}
body {
    background-color: var(--body-color);
    color: var(--text-color);
    transition: .4s;
}
h1,h2,h3 {
    color: var(--title-color);
    font-weight: var(--font-semibold);
}
ul {
    list-style: none;
}
a {
    text-decoration: none;
}
button {
    outline: none;
    border: none;
    cursor: pointer;
}
img {
    max-width: 100%;
    height: auto;
}
.z1 {
    z-index: 1001;
}
.z2 {
    z-index: 1000;
}
.z3 {
    z-index: 999;
}
.change-theme,
.tools svg {
    font-size: 1.25rem;
    cursor: pointer;
    transition: .3s;
}
.change-theme:hover,
.tools svg:hover {
    color: var(--first-color);
}
.tools {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
}
body.light-theme {
    --title-color: hsl(var(--second-hue), 15%, 15%);
    --text-color: hsl(var(--second-hue), 8%, 35%);
    --body-color: hsl(var(--second-hue), 100%, 99%);
    --container-color: #fff;
}
.light-theme .scroll-header {
    box-shadow: 0 2px 4px hsla(0, 0%, 1%, .1);
}
.light-theme .nav__menu {
    background-color: hsla(var(--second-hue), 32%, 90%, .8);
}
.light-theme .section__subtitle {
    color: var(--text-color);
}
.light-theme .home__social-link {
    box-shadow: 0 2px 8px hsla(var(--second-hue), 48%, 8%, .1);
}
.light-theme .home__social::after,
.light-theme .footer__social-link {
    background-color: var(--title-color);
}
.light-theme .home__social-link,
.light-theme .home__scroll,
.light-theme .button,
.light-theme .button:hover,
.light-theme .active-work,
.light-theme .footer__title,
.light-theme .footer__link,
.light-theme .footer__copy {
    color: var(--title-color);
}
.light-theme .about__box {
    box-shadow: 0 2px 8px hsla(var(--second-hue), 48%, 8%, .1);
}
.light-theme .skills__content,
.light-theme .work__card,
.light-theme .contact__card {
    box-shadow: 0 2px 16px hsla(var(--second-hue), 48%, 8%, .1);
}
.light-theme::-webkit-scrollbar {
    background-color: hsl(var(--second-hue), 8%, 66%);
}
.light-theme::-webkit-scrollbar-thumb {
    background-color: hsl(var(--second-hue), 8%, 54%);
}
.light-theme::-webkit-scrollbar-thumb:hover {
    background-color: hsl(var(--second-hue), 8%, 44%);
}
.container {
    max-width: 968px;
    margin-left: 1rem;
    margin-right: 1rem;
}
.grid {
    display: grid;
    gap: 1.25rem;
}
html[lang='ar'] .home__greeting,
html[lang='ar'] .home__name,
html[lang='ar'] .home__education,
html[lang='ar'] .button,
html[lang='ar'] .section__subtitle,
html[lang='ar'] .section__title,
html[lang='ar'] .about__title,
html[lang='ar'] .about__subtitle,
html[lang='ar'] .about__description,
html[lang='ar'] .skills__title,
html[lang='ar'] .skills__level,
html[lang='ar'] .contact__title,
html[lang='ar'] .contact__card-title,
html[lang='ar'] .contact__form-tag,
html[lang='ar'] .contact__form-input {
    font-family: 'Amiri', serif;
}
.main {
    overflow: hidden;
}
.section {
    padding: 4.5rem 0 1rem;
}
.section__title,
.section__subtitle {
    text-align: center;
}
.section__title {
    font-size: var(--h2-font-size);
    color: var(--first-color);
    margin-bottom: 2rem;
}
.section__subtitle {
    display: block;
    font-size: var(--smaller-font-size);
    color: var(--text-color-light);
}
.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background-color: var(--body-color);
    z-index: var(--z-fixed);
    transition: .4s;
}
.nav {
    height: var(--header-height);
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.nav__logo {
    color: var(--first-color);
    font-weight: var(--font-medium);
    transition: .4s;
}
.nav__logo .logo {
    display: flex;
    align-items: center;
}
.nav__logo:hover {
    color: var(--first-color-alt);
}
.nav__menu {
    position: fixed;
    bottom: -100%;
    background-color: hsl(var(--second-hue), 32%, 16%, .8);
    width: 90%;
    border-radius: 4rem;
    padding: 1rem 2.25rem;
    backdrop-filter: blur(10px);
    transition:bottom .4s ease;
}
.nav__menu.show {
    bottom: 1rem;
}
.nav__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.nav__link {
    color: var(--text-color);
    font-size: 1.25rem;
    padding: .4rem;
    display: flex;
    border-radius: 5rem;
}
.active-link {
    background: linear-gradient(180deg,
                hsla(var(--first-hue), var(--sat), var(--lig), 1),
                hsla(var(--first-hue), var(--sat), var(--lig),.2)
                );
    box-shadow: 0 0 16px hsla(var(--first-hue), var(--sat), var(--lig), .4);
    color: var(--title-color);
}
.scroll-header {
    box-shadow: 0 4px 4px hsla(0, 0%, 4%, .3);
}
.home__container {
    position: relative;
    /* row-gap: 4rem;
    padding-top: 2.5rem; */
    /* row-gap: 3rem;
    padding-top: 2rem; */
    row-gap: 4rem;
    padding-top: 3rem;
}
.home__data {
    text-align: center;
}
.home__greeting,
.home__education {
    font-size: var(--small-font-size);
    font-weight: var(--font-medium);
}
.home__greeting {
    display: block;
    color: var(--title-color);
    margin-bottom: .25rem;
}
.home__education {
    color: var(--text-color);
    margin-bottom: 1.5rem;
}
.home__name {
    font-size: var(--biggest-font-size);
    color: var(--first-color);
}
.home__img {
    width: 160px;
}
.home__handle {
    justify-self: center;
    width: 190px;
    height: 293px;
    background: linear-gradient(180deg, hsla(var(--first-hue), var(--sat), var(--lig), 1), hsla(var(--first-hue), var(--sat), var(--lig), .2));
    border-radius: 10rem 10rem 1rem 1rem;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    overflow: hidden;
}
.home__buttons {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1.5rem;
}
.home__social,
.home__scroll {
    position: absolute;
}
.home__social {
    bottom: 4rem;
    left: 0;
    display: grid;
    row-gap: .5rem;
}
.home__social-link {
    width: max-content;
    background-color: var(--container-color);
    color: var(--first-color);
    padding: .25rem;
    border-radius: .25rem;
    display: flex;
    font-size: 1rem;
    transition: .4s;
}
.home__social-link:hover {
    background-color: var(--first-color);
    color: #fff;
}
.home__social::after {
    content: '';
    width: 32px;
    height: 2px;
    background-color: var(--first-color);
    transform: rotate(90deg) translate(16px, 3px);
}
.home__scroll {
    color: var(--first-color);
    right: -1.5rem;
    bottom: 4rem;
    display: grid;
    row-gap: 2.25rem;
    justify-items: center;
}
.home__scroll-icon {
    font-size: 1.25rem;
}
.home__scroll-name {
    font-size: var(--smaller-font-size);
    transform: rotate(-90deg);
}
html[lang='ar'] .button {
    padding: .45rem 1.5rem;
}
.button {
    display: inline-block;
    background-color: var(--first-color);
    color: var(--body-color);
    padding: .75rem 1rem;
    border-radius: .5rem;
    font-weight: var(--font-medium);
    transition: .4s;
}
.button:hover {
    background-color: var(--first-color-alt);
    color: var(--body-color);
}
.button--ghost {
    background-color: transparent;
    color: var(--first-color);
    border: 2px solid var(--first-color);
}
.about__container {
    row-gap: 2.5rem;
}
.about__img {
    width: 220px;
    border-radius: 1.5rem;
    justify-self: center;
}
.about__data {
    text-align: center;
}
.about__info {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: .5rem;
    margin-bottom: 2rem;
}
.about__box {
    background-color: var(--container-color);
    padding: .75rem .5rem;
    border-radius: .75rem;
}
.about__icon {
    font-size: 1.5rem;
    color: var(--first-color);
    margin-bottom: .5rem;
}
.about__title {
    font-size: var(--small-font-size);
}
.about__subtitle {
    font-size: var(--tiny-font-size);
}
.about__description {
    margin-bottom: 2rem;
}
html[lang='ar'] .about__description {
    direction: rtl;
}
.skills__container {
    row-gap: 2rem;
    padding-top: 1rem;
}
.skills__content {
    background-color: var(--container-color);
    padding: 1.5rem;
    border-radius: 1.25rem;
}
.skills__title {
    font-size: var(--normal-font-size);
    font-weight: var(--font-medium);
    color: var(--first-color);
    text-align: center;
    margin-bottom: 1.5rem;
}
.skills__box {
    display: flex;
    justify-content: center;
    column-gap: 2.5rem;
}
.skills__group {
    display: grid;
    align-content: flex-start;
    row-gap: 1rem;
}
.skills__data {
    display: flex;
    column-gap: .5rem;
}
.skills .bxs-badge-check {
    font-size: 1rem;
    color: var(--first-color);
}
.skills__name {
    font-size: var(--normal-font-size);
    font-weight: var(--font-medium);
    line-height: 18px;
}
.skills__level {
    font-size: var(--tiny-font-size);
}
.work__container {
    padding-top: 1rem;
}
.work__filters {
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: .75rem;
    margin-bottom: 2rem;
}
.work__item {
    cursor: pointer;
    color: var(--title-color);
    padding: .25rem .75rem;
    font-weight: var(--font-medium);
    border-radius: .5rem;
}
.work__card {
    background-color: var(--container-color);
    padding: 1rem;
    border-radius: 1rem;
}
.work__img {
    border-radius: 1rem;
    margin-bottom: 1rem;
}
.work__title {
    font-size: var(--normal-font-size);
    font-weight: var(--font-medium);
    margin-bottom: .25rem;
}
.work__button {
    width: max-content;
    color: var(--first-color);
    font-size: var(--small-font-size);
    display: flex;
    align-items: center;
    column-gap: .25rem;
}
.work__button:hover .work__icon {
    transform: translateX(.25rem);
}
.work__icon {
    font-size: 1rem;
    transition: .4s;
}
.active-work {
    background-color: var(--first-color);
    color: var(--body-color);
}
.contact__container {
    row-gap: 3rem;
    padding-bottom: 3rem;
}
.contact__title {
    text-align: center;
    font-size: var(--h3-font-size);
    margin-bottom: 1.5rem;
}
.contact__info {
    display: grid;
    gap: 1rem;
}
.contact__card {
    background-color: var(--container-color);
    padding: 1rem;
    text-align: center;
    border-radius: .75rem;
}
.contact__card-icon {
    font-size: 2rem;
    color: var(--title-color);
    margin-bottom: .25rem;
}
.contact__card-title,
.contact__card-data {
    font-size: var(--small-font-size);
}
.contact__card-title {
    font-weight: var(--font-medium);
}
.contact__card-data {
    display: block;
    margin-bottom: .75rem;
}
.contact__button {
    color: var(--first-color);
    font-size: var(--small-font-size);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    column-gap: .25rem;
}
.contact__button:hover .contact__button-icon {
    transform: translateX(.25rem);
}
.contact__button-icon {
    font-size: 1rem;
    transition: .4s;
}
.contact__form-div {
    position: relative;
    margin-bottom: 2rem;
    height: 4rem;
}
html[lang='ar'] .contact__form-input {
    direction: rtl;
}
.contact__form-input {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 2px solid var(--text-color-light);
    background: none;
    color: var(--text-color);
    outline: none;
    padding: 1.5rem;
    z-index: 1;
    border-radius: .75rem;
}
.contact__form-input:focus {
    border-color: var(--first-color);
    
}
.contact__form-tag {
    position: absolute;
    top: -.75rem;
    font-size: var(--smaller-font-size);
    padding: .25rem;
    background-color: var(--body-color);
    z-index: 10;
}
html[lang='ar'] .contact__form-tag {
    right: 1.25rem;
}
html[lang='en'] .contact__form-tag {
    left: 1.25rem;
}
.contact__form-area {
    height: 11rem;
}
.contact__form-area textarea {
    resize: none;
}
.footer {
    background-color: var(--first-color);
}
.footer__container {
    padding: 3rem 0 6rem;
}
.footer__social {
    display: flex;
    justify-content: center;
    column-gap: 1.25rem;
}
.footer__social-link {
    background-color: var(--body-color);
    color: var(--first-color);
    padding: .25rem;
    font-size: 1rem;
    display: inline-flex;
    border-radius: .25rem;
}
.footer__copy {
    display: block;
    margin-top: 2.5rem;
    color: var(--container-color);
    text-align: center;
    font-size: var(--smaller-font-size);
}
::-webkit-scrollbar {
    width: .6rem;
    background-color: hsl(var(--second-hue), 8%, 38%);
    border-radius: .5rem;
}
::-webkit-scrollbar-thumb {
    background-color: hsl(var(--second-hue), 8%, 26%);
    border-radius: .5rem;
}
::-webkit-scrollbar-thumb:hover {
    background-color: hsl(var(--second-hue), 8%, 20%);
}
/* for small devices */
@media screen and (max-width: 320px){
    .nav__menu {
        padding: 1rem 1.5rem;
    }
    .home__buttons {
        flex-direction: column;
    }
    .home__handle {
        width: 150px;
        height: 253px;
    }
    .home__img {
        width: 130px;
    }
    .about__info {
        grid-template-columns: repeat(2, 1fr);
    }
    .skills__box {
        column-gap: 1rem;
    }
    .skills__name {
        font-size: var(--small-font-size);
    }
    .work__item {
        font-size: var(--small-font-size);
    }
    .work__filters {
        column-gap: .25rem;
    }
}
/* for medium devices */
@media screen and (min-width: 576px){
    .nav__menu {
        width: 328px;
        left: 0;
        right: 0;
        margin: 0 auto;
    }
    html[lang='ar'] .home__container {
        row-gap: 3rem;
    }
    .home__container {
        row-gap: 4rem;
        padding-top: 5rem;
    }
    .about__info {
        grid-template-columns: repeat(3, 140px);
        justify-content: center;
    }
    .about__description {
        padding: 0 5rem;
    }
    .skills__container {
        justify-content: center;
    }
    .skills__content {
        padding: 2rem 4rem;
    }
    .work__container {
        justify-content: center;
    }
    .work__img {
        width: 295px;
    }
    .contact__info {
        grid-template-columns: 300px;
        justify-content: center;
    }
    .contact__form {
        width: 360px;
        margin: 0 auto;
    }
}
@media screen and (min-width: 767px) {
    .work__container {
        grid-template-columns: repeat(2, max-content);
    }
    .contact__container {
        grid-template-columns: repeat(2, max-content);
        justify-content: center;
        column-gap: 3rem;
    }
}
/* for large devices */
@media screen and (min-width: 992px) {
    .container {
        margin-left: auto;
        margin-right: auto;
    }
    .section {
        padding: 4.5rem 0 1rem;/*padding: 6.5rem 0 1rem;*/
    }
    .section__title {
        margin-bottom: 3.5rem;
    }
    .nav {
        height: calc(var(--header-height) + 1rem);
    }
    .home__handle {
        width: 290px;
        height: 400px;
    }
    .home__img {
        width: 220px;
    }
    .home__social-link {
        padding: .4rem;
        font-size: 1.25rem;
    }
    .home__container {
        padding-top: 1rem;
    }
    .home__social::after {
        transform: rotate(90deg) translate(16px, 0);
    }
    .home__scroll-icon {
        font-size: 2rem;
    }
    .about__container {
        grid-template-columns: repeat(2, 1fr);
        align-items: center;
        column-gap: 4rem;
    }
    .about__img {
        /* width: 350px; */
        width: 418px;
    }
    html[lang='ar'] .about__img {
        width: 350px;
    }
    .about__data {
        text-align: initial;
    }
    .about__info {
        justify-content: initial;
    }
    .about__box {
        text-align: center;
        padding: 1rem 1.25rem;
    }
    .about__description {
        padding: 0 4rem 0 0;
        margin-bottom: 2.5rem;
    }
    .skills__container {
        grid-template-columns: repeat(2, 350px);
        column-gap: 3rem;
    }
    .work__container {
        gap: 3rem;
    }
    .work__card {
        padding: 1.25rem;
    }
    .work__img {
        margin-bottom: 1rem;
    }
    .work__title {
        margin-bottom: .5rem;
    }
    .contact__container {
        column-gap: 6rem;
    }
    .footer__social-link {
        font-size: 1.25rem;
        padding: .4rem;
        border-radius: .5rem;
    }
}
.icon {
    animation: float 5s ease-in-out infinite;
    -webkit-animation: float 5s ease-in-out infinite;
}
.arrow__icone {
    animation: arrow 2s ease-in-out infinite;
    -webkit-animation: arrow 2s ease-in-out infinite;
}
@keyframes arrow {
    0%,100% {
        transform: translateX(0);
    }
    50% {
        transform: translateX(15px);
    }
}
.icon:nth-child(2) {
    animation-delay: 1s;
}
.icon:nth-child(3) {
    animation-delay: 2s;
}
@keyframes float {
    0%,100% {
        transform: translateY(0);
    }
    50% {
        transform: translateY(-15px);
    }
}