
.nav-sitting {
    position: fixed;
    top: 50px;
    right: 0;
    width: 80px;
    height: 175px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: default;
}
.nav-sitting .nav-content {
    display: flex;
    align-items: center;
    justify-content: center;
    transform: rotate(-45deg);
}
.nav-content .toggle-btn,
.nav-content span {
    height: 30px;
    width: 30px;
    background-color: var(--text-color-light);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
}
.nav-content .toggle-btn {
    font-size: var(--h2-font-size);
    color: var(--text-color);
    z-index: 100;
    cursor: pointer;
    background-color: transparent;
    transform: rotate(45deg);
    box-shadow: 0 0 8px 0px var(--first-color-alt);
    transition: all 0.6s ease;
}
.nav-content .toggle-btn:hover,
.nav-sitting.open .toggle-btn {
    color: var(--first-color);
}
.nav-content span {
    position: absolute;
    transition: all 0.6s ease;
    opacity: 0;
}
.nav-content span::before {
    content: '';
    width: 30px;
    height: 30px;
    border-radius: 50%;
}
.nav-content span.active::before {
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px, var(--title-color) 0px 0px 0px 3px;
}
.nav-sitting.open .nav-content span {
    transform: rotate(calc(var(--i) * (360deg / 8))) translateY(60px);
    opacity: 1;
}
.nav-content span {
    transform: rotate(45deg);
}
.nav-content span:nth-of-type(1) {
    background-color: hsl(250, 66%, 75%);
}
.nav-content span:nth-of-type(2) {
    background-color: hsl(207, 90%,72%);
}
.nav-content span:nth-of-type(3) {
    background-color: hsl(160, 100%, 32%);
}
.nav-content span:nth-of-type(4) {
    background-color: hsl(348, 83%, 47%);
}
.nav-content span:nth-of-type(5) {
    background-color: hsl(4, 90%, 58%);
}
@media screen and (max-width: 320px) {
    .nav-sitting {
        top: 30px;
        right: -20px;
    }
    .nav-sitting.open .nav-content span {
        transform: rotate(calc(var(--i) * (360deg / 8))) translateY(45px);
    }
    .nav-content .toggle-btn,
    .nav-content span,
    .nav-content span::before {
        height: 25px;
        width: 25px;
    }
}
@media screen and (min-width: 330px) {
    .nav-sitting {
        top: 30px;
        right: -15px;
    }
    .nav-sitting.open .nav-content span {
        transform: rotate(calc(var(--i) * (360deg / 8))) translateY(45px);
    }
    .nav-content .toggle-btn,
    .nav-content span,
    .nav-content span::before {
        height: 25px;
        width: 25px;
    }
}
@media screen and (min-width: 767px) {
    .nav-sitting {
        top: 40px;
        right: -10px;
    }
    .nav-sitting.open .nav-content span {
        transform: rotate(calc(var(--i) * (360deg / 8))) translateY(50px);
    }
    .nav-content .toggle-btn,
    .nav-content span,
    .nav-content span::before {
        height: 30px;
        width: 30px;
    }
}
@media screen and (min-width: 992px) {
    .nav-sitting {
        top: 55px;
        right: 0px;
    }
}